export default {
  props: ["content", "display"],
  data: function() {
    return {
      fadeIn: false,
      fadeOut: false,
      isShow: false
    };
  },
  created: function() {
    console.log(this.content, this.display);
  },
  watch: {
    display: function() {
      if (this.display) {
        this.openDialog();
      }
    }
  },
  methods: {
    openDialog: async function() {
      if (!this.isShow) {
        this.isShow = true;
        this.fadeIn = true;
      } else {
        this.fadeIn = false;
        this.fadeOut = true;
        this.display = false;
        await this.pause(150);
        this.isShow = false;
        this.fadeOut = false;
      }
    },
    pause: function(time) {
      return new Promise((resolve, reject) => {
        setTimeout(function() {
          resolve("ok");
        }, time);
      });
    }
  }
};



